<script>
	//MV = MOUSE VIEWER
	import { spring } from 'svelte/motion'

	export let color = "red"

	let Coords = spring({ x: 50, y: 50 }, {
		stiffness: 0.8,
		damping: 0.8
	});

	document.addEventListener('DOMContentLoaded', function(){
		const R = firebase.database().ref("A"+color)
		R.on('value', snap =>{
			console.log("receiving from firebase", color)
			Coords.set({
				x:Number(snap.child('x').val()),
				y:Number(snap.child('y').val())
				})
		})
	})
</script>

<svg>	
	<circle style="fill:{color}" cx={$Coords.x} cy={$Coords.y} r="10"/>
</svg>


<style>
svg {position:absolute; pointer-events:none; margin: -8px; overflow:visible; z-index:9999;}
</style>